
.info {
  padding: 2px;
  width: 50%;
  height: 4vh;
  margin: 10px auto;
  opacity: 0.7;
  border-radius: 10px;
  background-color: yellow;
  text-align: center;
  font-size: 1vw;
}
.other {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-flow: row;
          flex-flow: row;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 4px;
  width: 50%;
  padding: 4px;
  border-radius: 6px;
}
.other > div:nth-child(1) {
    width: 16px;
    height: 16px;
    border-radius: 50%;
    background-color: attr(data-color);
}
.other > div:nth-child(2) {
    position: relative;
    width: 70%;
    border-radius: 10px;
    background-color: attr(data-color);
    padding: 10px;
}
.myself {
  margin-left: 50%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: reverse;
      -ms-flex-flow: row-reverse;
          flex-flow: row-reverse;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  width: 50%;
  padding: 4px;
  border-radius: 6px;
  border-radius: 10px;
}
.myself > div:nth-child(1) {
    width: 16px;
    height: 16px;
    border-radius: 50%;
    background-color: attr(data-color, color);
}
.myself > div:nth-child(2) {
    width: 70%;
    border-radius: 10px;
    background-color: attr(data-color, color);
    text-align: center;
    padding: 10px;
}



